import { createSlice } from '@reduxjs/toolkit';

export const themeModeSlice = createSlice({
  name: 'ThemeMode',
  initialState: {
    themeMode: 'light',
  },
  reducers: {
    setThemeMode: (state, action) => {
      state.themeMode = action.payload;
    },
  },
});

export const { setThemeMode } = themeModeSlice.actions;
